export default {
  name: 'wxParseVideo',
  props: {
    node: {},
  },
  data(){
	  return{
		  playState:true,
		  videoStyle:'width: 100%;'
	  }
  },
  methods:{
	  play(){
		  console.log('点击了video 播放')
		  //显示播放器并播放播放器
		  this.playState = !this.playState
	  }
  },
  mounted() {
	  //捕获侧滑菜单的遮盖行为，隐藏video
  	uni.$on('slideMenuShow',e=>{
		console.log('捕获事件：'+e)
		if(e == 'show' && this.playState){
			//正在播放则停止
			this.playState = false
		}
	})
  }
};
